html {
  overflow-y: scroll;
}

.drawer__toolbar {
  text-align: right;
}

header {
  position: relative !important;
}

header a {
  margin-left: -6px !important;
}

header h6 {
  flex: 20;
  text-align: left;
}

header.MuiAppBar-colorPrimary {
  background: black;
}

div.MuiToolbar-root {
  display: flex;
  justify-content: space-between;
  padding-right: 4px;
  padding-left: 8px;
}

div.MuiToolbar-root .MuiTypography-root {
  display: inline;
}

div.MuiToolbar-root .MuiTypography-root * {
  vertical-align: text-top;
}

button.MuiButtonBase-root {
  margin-right: 0;
}

div.MuiDrawer-paperAnchorLeft {
  left: auto;
  right: 0;
}

div.MuiDrawer-paperAnchorDockedLeft {
  border-left: 1px solid rgba(0, 0, 0, 0.12);
  border-right: none;
}

div.MuiListItemIcon-root {
  min-width: 30px;
}

.input__container {
  display: flex;
  flex-direction: column;
  padding-top: 1.2rem;
}

.input__container input[type=text] {
  color: white;
  display: inline-flex;
  width: 72%;
  font-size: 1.7rem;
  border-radius: 4px;
  border: 0.2rem solid #1c1c1c;
  background: #1c1c1c;
  padding: 0 0.3rem;
}

.input__container input[type=text]:hover,
.input__container select:hover {
  border: 0.2rem solid white !important;
}

.select__container .MuiInputBase-root {
  width: 83%;
}

.input__container select {
  appearance: none;
  color: white;
  display: inline-flex;
  font-size: 1.7rem;
  border-radius: 4px;
  background-color: #1c1c1c !important;
  border: 0.2rem solid #1c1c1c;
  margin: 0 0.2rem;
  padding: 0 0.2rem;
  width: 100%;
  font-family: inherit;
  cursor: inherit;
  line-height: inherit;
}
.input__container option {
  background-color: #1c1c1c !important;
  color: white;
}

.input__container .select__container {
  display: flex;
  margin-top: 0.4rem;
}

.input__container .MuiInputLabel-shrink {
  transform: none;
  transform-origin: initial;
}

.input__container .MuiInputLabel-formControl {
  position: relative;
}

.input__container .MuiInputLabel-root {
  font-size: 1rem;
  text-align: left;
  color: white;
  padding-bottom: 0.4em;
}

.input__container .MuiInputAdornment-root,
.input__container .MuiInputAdornment-root p {
  color: white;
}

.MuiTypography-root .info .MuiAlert-icon,
.MuiTypography-root .info .MuiAlert-action,
.input__container .MuiAlert-icon,
.input__container .MuiAlert-action {
  display: none;
}

.input__container label + .MuiInput-formControl {
  margin-top: 0.4rem;
}

.input__container .MuiInput-underline::before,
.input__container .MuiInput-underline::after,
.input__container .MuiInput-underline:hover:not(.Mui-disabled):before {
  border: 0;
}

.text-vertical-center * {
  vertical-align: text-top;
}

.input__container .btn-info {
  min-width: 0;
  padding: 0;
  margin: 0 6px;
}

.input__container .MuiAlert-message p:last-child {
  padding-bottom: 0;
  margin-block-end: 0;
}

.input__group .insulinunits__button__addproportionatecarbs,
.screen__container .proportionatecarbs__button__addtoinsulin {
  display: flex;
  justify-content: flex-end;
  padding-top: 0.3rem;
}

.input__group .insulinunits__button__addproportionatecarbs button,
.input__group .insulinunits__button__addproportionatecarbs a,
.screen__container .proportionatecarbs__button__addtoinsulin button,
.screen__container .proportionatecarbs__button__addtoinsulin a {
  flex: 1;
}

.input__group .insulinunits__button__addproportionatecarbs {
  padding-top: 0.3rem;
}

.input__group .insulinunits__button__addproportionatecarbs .MuiButtonBase-root {
  width: initial !important;
}

.input__group .MuiAccordion-root,
.input__group .MuiAccordion-root.Mui-expanded {
  margin-top: 2rem;
  box-shadow: none;
}

.input__group .MuiAccordionSummary-root,
.input__group .MuiAccordionSummary-root.Mui-expanded {
  background: lightgrey;
  margin: 0;
  min-height: 0;
  padding: 6px 8px;
  border-radius: 4px;
}

.input__group .MuiAccordionSummary-content,
.input__group .MuiAccordionSummary-content.Mui-expanded {
  font-size: 0.875rem;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  margin: auto;
  display: flex;
  line-height: 1.75;
  padding: 0;
  justify-content: center;
}

.input__group .MuiIconButton-root {
  padding: 0 10px;
}

.input__group .MuiAccordionDetails-root {
  flex-direction: column;
  padding: 0.5rem 0;
}

.screen__container {
  max-width: 400px;
  margin: auto;
  padding: 0 8px;
}

.screen__container .button {
  background: #1c1c1c;
  border: 0.2rem solid #1c1c1c;
  margin: 0 0.2rem;
  color: white;
}

.screen__container .button.Mui-disabled {
  color: rgba(255,255,255, 0.26);
}

.MuiTypography-root.section__divider {
  font-weight: bold;
  font-size: 1.3em;
  text-align: left;
  padding-top: 2em;
}

.MuiTypography-root.section__divider .info {
  text-transform: none;
  white-space: normal;
}

.insulinunits_input.input__group {
  /* padding-top: 1em; */
}

.proportionatecarbs__input__carbsperweight__container,
.proportionatecarbs__input__amountconsumed__container {
  display: flex;
  flex-direction: column;
}

.proportionatecarbs__input__carbsperweight__container > *:first-child,
.proportionatecarbs__input__amountconsumed__container > *:first-child {
  /* padding-right: 1em; */
}

.proportionatecarbs__input__carbsperweight__container > *:last-child,
.proportionatecarbs__input__amountconsumed__container > *:last-child {
  /* padding-left: 1em; */
}
